.App {
  text-align: center;
}
body{
  
  background-color: black;
}
.container{
  width: 800px;
  margin: 0 auto;
}
.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid gray;
}
.header img{
  width: 120px;
  height: auto;
}
.menu-list{
  column-gap: 10px;
  display: flex;
  justify-content: space-between;
  list-style: none;
}
.menu-list a{
    text-decoration: none;
    color: rgb(255, 255, 255);
}




.maincontent{
  padding-bottom:51px;
}

.number{  
  font-size: 288px;
  text-align: center;
  font-weight: 700;
  color: white;
}

.buttons{
  display: flex;
  justify-content: space-evenly;
}
button{
  width: 56px;
  height: 58px;
  border-radius: 27px;
}
.plus{
  background-color: green;
}
.minus{
  background-color: red;
}




.footer{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.footer-logo{
  height: 22px;
}
.footer-list{
  display: flex;
  column-gap: 10px;
  list-style: none;
}
.footer-list a{
  text-decoration: none;
  color: white;
}

.footer-rights p{
  color: white;
  padding-left: 125px;
}

